import React from "react"
import { graphql, Link } from "gatsby"
import { Layout, Row, Col, Menu } from 'antd';
import '../static/style';
import { Header } from "../components/Header";

export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark
  return (
    <Layout style={{display: 'flex', minHeight: '100vh'}}>
      <Header />
      <Layout style={{ marginTop: "64px", flex: 1}}>
        <Layout.Sider width='20%'>
          <Menu
            mode="vertical"
            style={{ height: '100%', borderRight: 0}}
            theme="dark"
            selectedKeys={[frontmatter.slug]}
          >
              <Menu.Item key="/docs/getting-started">
            <Link to="/docs/getting-started/">
                Getting Started</Link></Menu.Item>
            {/* <Link to="/docs/midival-in-nodejs">
              <Menu.Item key="/docs/midival-in-nodejs">MIDIVal in Node.js</Menu.Item>
            </Link> */}
            </Menu>
        </Layout.Sider>
        <Layout.Content className="site-layout" style={{ padding: '0 50px', marginTop: 64 }}>
          <Row>
            <Col span={24}>
            <div className="documentation-article">
          <h1>{frontmatter.title}</h1>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </div>
            </Col>
          </Row>
        </Layout.Content>
        </Layout>
    </Layout>
  )
}
export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        slug
        title
      }
    }
  }
`